// MUI Package Module
import { Box, styled, Typography } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";
import Icons from "components/controls/Icons";

// Constant
import { CODE, COLOR } from "common";

// API
import { deleteData, postData } from "common/communication";

/** STYLE Ï†ïÏùò */
// Ïπ¥Îìú Ïª¥Ìè¨ÎÑåÌä∏ Ï†ÑÏ≤¥ ÏòÅÏó≠
const Whole = styled(Box)({
	width: 211,
	borderRadius: 15,
	display: "flex",
	flexDirection: "column",
	marginRight: 20,
	marginBottom: 15,
});

// ÏÜåÏÑ§ Ïª§Î≤Ñ Ïù¥ÎØ∏ÏßÄ ÏòÅÏó≠
const Cover = styled(Box)({
	width: "100%",
	minHeight: 290,
	borderRadius: 15,
	backgroundColor: COLOR.PURPLE,
	marginBottom: 9,
  	"&:hover": {
		opacity: 0.7,
		cursor: "pointer",
	},
});

// ÏÜåÏÑ§ ÏÑ§Î™Ö ÏòÅÏó≠ (Ï†úÎ™©, ÌïúÏ§ÑÏÜåÍ∞ú, ÌÉúÍ∑∏, Ï¢ãÏïÑÏöî)
const InfoBox = styled(Box)({
	flex: "1",
	display: "flex",
	flexDirection: "column",
});

// Ï†úÎ™©, ÌïúÏ§ÑÏÜåÍ∞ú, Ï¢ãÏïÑÏöî ÏòÅÏó≠
const InfoItemBox = styled(Box)({
	flex: "1",
	display: "flex",
	paddingLeft: 5,
});

// Ï†úÎ™© ÏòÅÏó≠
const TitleBox = styled(Box)({
	width: "80%",
});

// Ï¢ãÏïÑÏöî ÏòÅÏó≠
const LikedBox = styled(Box)({
	marginLeft: 'auto',
	display: "flex",
});

// ÏÜåÏÑ§ Ï†úÎ™©
const Title = styled(Typography)({
	fontSize: 15,
	paddingTop: 2,
	whiteSpace: "noWrap",
	overflow: "hidden",
	textOverflow: "ellipsis",
});

// Ï¢ãÏïÑÏöî Ïàò
const CountLike = styled(Typography)({
	fontSize: 11,
	display: "flex",
	alignItems: "center",
	marginRight: 2,
});

// ÏÜåÏÑ§ ÌïúÏ§ÑÏÜåÍ∞ú
const Description = styled(Typography)({
	fontSize: 11,
	color: COLOR.GRAY,
	whiteSpace: "noWrap",
	overflow: "hidden",
	textOverflow: "ellipsis",
	height: 15,
});

// ÌÉúÍ∑∏ ÏòÅÏó≠
const TagBox = styled(Box)({
	marginTop: 3,
	minHeight: 60,
});

/** ÏÜåÏÑ§ Ï†ïÎ≥¥Î•º Îã¥Í≥†ÏûàÎäî Ïπ¥Îìú ÌòïÏãù Ïª¥Ìè¨ÎÑåÌä∏ (Î©îÏù∏ ÌôîÎ©¥, ÏûëÍ∞Ä ÏÉÅÏÑ∏ Ï†ïÎ≥¥ ÌåùÏóÖÏóêÏÑú ÏÇ¨Ïö©) */
const NovelCard = (props) => {

  // ÌïòÌä∏ ÏïÑÏù¥ÏΩò ÎàåÎ†ÄÏùÑ Îïå Ïã§ÌñâÎê† Í∏∞Îä• Ìï®Ïàò
  const pickNovel = () => {
    if (props.pick_yn === "N") {
      postData('novel/postPickNovel', {
        main_novel_seqno: props.main_seqno,
        user_id: props.user_id,
      })
      .then(() => {
        props.getNovelData && props.getNovelData();                         // Main ÌéòÏù¥ÏßÄ ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
        props.getIncompleteNovelData && props.getIncompleteNovelData();     // Main ÌéòÏù¥ÏßÄ ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
        props.getPickNovels && props.getPickNovels();                       // Ï∞úÌïú ÏÜåÏÑ§ ÌéòÏù¥ÏßÄ ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
        props.getPickIncompleteNovels && props.getPickIncompleteNovels();   // Ï∞úÌïú ÏÜåÏÑ§ ÌéòÏù¥ÏßÄ ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
      })
      .catch((err) => {
        console.log(err);
      })
    } else if (props.pick_yn === "Y") {
      deleteData('novel/deletePickNovel', {
        main_novel_seqno: props.main_seqno,
        user_id: props.user_id,
      })
      .then(() => {
        props.getNovelData && props.getNovelData();                         // Main ÌéòÏù¥ÏßÄ ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
        props.getIncompleteNovelData && props.getIncompleteNovelData();     // Main ÌéòÏù¥ÏßÄ ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
        props.getPickNovels && props.getPickNovels();                       // Ï∞úÌïú ÏÜåÏÑ§ ÌéòÏù¥ÏßÄ ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
        props.getPickIncompleteNovels && props.getPickIncompleteNovels();   // Ï∞úÌïú ÏÜåÏÑ§ ÌéòÏù¥ÏßÄ ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Ï°∞Ìöå
      })
      .catch((err) => {
        console.log(err);
      })
    }
  };


	return (
		<Whole>
			<Cover
				onClick={props.onClick}
				style={{
					// props.cover_image => ÌååÏùºÎ™ÖÏù¥ ÌïúÍ∏ÄÏùº Í≤ΩÏö∞ encodingÏùÑ ÌÜµÌï¥ Ïò¨Î∞îÎ•∏ Í≤ΩÎ°ú Ï∂îÏ∂ú
					backgroundImage: `url(${process.env.REACT_APP_COVER_IMAGE_DIRECTORY}/${encodeURIComponent(props.cover_image)})`,
					backgroundSize: "cover",
				}}
			/>
			<InfoBox>
				<InfoItemBox>
					<TitleBox>
						<Title>{props.title}</Title>
					</TitleBox>
					<LikedBox>
						{props.pick_yn &&
							(props.pick_yn === "Y" ? (
								<Icons
									type={CODE.ICON.FILLHEART}
									// ÌïòÌä∏ ÏïÑÏù¥ÏΩò ÎàåÎ†ÄÏùÑ Îïå Ïã§ÌñâÎê† Í∏∞Îä•
									pickNovel={pickNovel}
								/>
							) : (
								<Icons
									type={CODE.ICON.HEART}
									// ÌïòÌä∏ ÏïÑÏù¥ÏΩò ÎàåÎ†ÄÏùÑ Îïå Ïã§ÌñâÎê† Í∏∞Îä•
									pickNovel={pickNovel}
								/>
							))}
					</LikedBox>
				</InfoItemBox>
				<InfoItemBox>
					<Description>
						{props.description && props.description.replace(/\\n/g, " ")}
					</Description>
				</InfoItemBox>
				{props.genre_1 && (
					<TagBox>
						<Buttons
							type={CODE.BUTTON.TAG}
							name={props.genre_1}
							backgroundColor={`#${props.genre_1_color}`}
						/>
						{props.genre_2 && (
							<Buttons
								type={CODE.BUTTON.TAG}
								name={props.genre_2}
								backgroundColor={`#${props.genre_2_color}`}
							/>
						)}
						<Buttons
							type={CODE.BUTTON.TAG}
							name={props.keyword_1}
							backgroundColor={`#${props.keyword_1_color}`}
						/>
						{props.keyword_2 && (
							<Buttons
								type={CODE.BUTTON.TAG}
								name={props.keyword_2}
								backgroundColor={`#${props.keyword_2_color}`}
							/>
						)}
						{props.keyword_3 && (
							<Buttons
								type={CODE.BUTTON.TAG}
								name={props.keyword_3}
								backgroundColor={`#${props.keyword_3_color}`}
							/>
						)}
					</TagBox>
				)}
			</InfoBox>
		</Whole>
	);
};

export default NovelCard;