// MUI Package Module
import styled from "@emotion/styled";
import {
	Favorite,
	FavoriteBorder,
	Close,
	Pets,
	Search,
	ThumbUpOffAlt,
	ThumbUpAlt,
} from "@mui/icons-material";

// Constant
import { CODE, COLOR } from "common";

// Ï∞ú ÎàåÎ†ÄÏùÑ Îïå ÌïòÌä∏ ÏïÑÏù¥ÏΩò
const FillHeart = styled(Favorite)({
	color: COLOR.HEART_PINK,
	cursor: "pointer",
});

// Ï∞ú ÎàÑÎ•¥Í∏∞ Ï†Ñ Îπà ÌïòÌä∏
const Heart = styled(FavoriteBorder)({
	color: COLOR.HEART_PINK,
	cursor: "pointer",
});

// Ï¢ãÏïÑÏöî ÎàÑÎ•¥Í∏∞ Ï†Ñ Î≤ÑÌäº
const Like = styled(ThumbUpOffAlt)({
	color: COLOR.MUSTARD,
	cursor: "pointer",
});

// Ï¢ãÏïÑÏöî ÎàåÎ†ÄÏùÑ Îïå Î≤ÑÌäº
const Liked = styled(ThumbUpAlt)({
	color: COLOR.MUSTARD,
	cursor: "pointer",
});

const _getIconComp = (type) => {
	let Component;
	switch (type) {
		case CODE.ICON.FILLHEART:
			Component = FillHeart;
			break;
		case CODE.ICON.HEART:
			Component = Heart;
			break;
		case CODE.ICON.CLOSE:
			Component = Close;
			break;
		case CODE.ICON.CAT:
			Component = Pets;
			break;
		case CODE.ICON.SEARCH:
			Component = Search;
			break;
		case CODE.ICON.LIKE:
			Component = Like;
			break;
		case CODE.ICON.LIKED:
			Component = Liked;
			break;
	}
	return Component;
};

// Icon Component
const Icons = (props) => {
	const Icon = _getIconComp(props.type);

	return (
		<Icon
			sx={{
				color: props.color !== undefined ? props.color : "transparent",
				":hover": {
					cursor: "pointer",
					color: props.h_color,
				},
			}}
			type={props.type}
			onClick={() => {
				props.pickNovel && props.pickNovel();
			}}
		/>
	);
};
export default Icons;